.advantages {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
}

.advantages__heading {
  display: block;
  width: 210px;
  min-height: 143px;
  margin: 0;
  margin-top: 41px;
  margin-bottom: 65px;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .advantages__heading {
    width: 590px;
    margin-top: 50px;
    margin-bottom: 98px;
  }
}

@media (min-width: @desktop-width) {
  .advantages__heading {
    width: 735px;
    margin-top: 67px;
    margin-bottom: 171px;
  }
}

.advantages__text {
  font-size: 24px;
  line-height: 28px;
  font-weight: 300;
  color: @bg-dark;
  text-align: center;
  margin: 0;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .advantages__text {
    font-size: 40px;
    line-height: 48px;
  }
}

@media (min-width: @desktop-width) {
  .advantages__text {
    font-size: 50px;
    line-height: 60px;
  }
}

.advantages__text--top {
  margin-bottom: 31px;
}

@media (min-width: @tablet-width) {
  .advantages__text--top {
    margin-bottom: 0;
  }
}


.advantages__wrapper {
  display: flex;
  position: relative;
  width: 100%;
  justify-content: center;
  background-color: @bg-light;
  padding-bottom: 279px;

  &::before {
    content: "";
    position: absolute;
    top: -30px;
    left: 0;
    width: 100%;
    height: 70px;
    background-color: #f2f2f2;
    background-image: url("../img/bg-triangle-white.svg");
    background-repeat: no-repeat;
    background-position: top left;
    background-size: 100% auto;
  }
}

@media (min-width: @tablet-width) {
  .advantages__wrapper {
    padding-bottom: 0;

    &::before {
      position: absolute;
      height: 90px;
      top: -60px;
    }
  }
}

@media (min-width: @desktop-width) {
  .advantages__wrapper {
    &::before {
      position: absolute;
      height: 120px;
      top: -100px;
    }
  }
}

.advantages__list {
  display: flex;
  width: 320px;
  position: relative;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  margin: 0;
  margin-top: 30px;
  padding: 0;

  &::after {
    content: "";
    position: absolute;
    bottom: -322px;
    left: 81px;
    background-image: url("../img/iphone-screen-mobile@1x.png");
    width: 157px;
    height: 320px;
    background-repeat: no-repeat;
    background-color: @bg-light;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/iphone-screen-mobile@2x.png");
    }
  }
}

@media (min-width: @tablet-width) {
  .advantages__list {
    flex-direction: column;
    align-items: flex-end;
    min-width: 660px;
    margin-top: 1px;
    padding: 0;

    &::after {
      background-image: url("../img/iphone-screen-3-tablet@1x.png");
      width: 268px;
      height: 547px;
      right: 0;
      left: 19px;
      top: -49px;
      background-color: transparent;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../img/iphone-screen-3-tablet@2x.png");
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .advantages__list {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    min-width: 1200px;
    padding: 0;
    margin: 0;

    &::after {
      display: none;
    }
  }
}

.advantages__item {
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  justify-content: center;
  width: 260px;
  min-height: 102px;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 12px;
  padding: 0;

  &:last-child {
    margin-bottom: 40px;
  }
}

@media (min-width: @tablet-width) {
  .advantages__item {
    width: 310px;
    justify-content: left;
    align-content: flex-start;
    margin-top: 22px;
    margin-right: 20px;
    margin-bottom: 14px;

    &:last-child {
      margin-bottom: 11px;
    }
  }
}

@media (min-width: @desktop-width) {
  .advantages__item {
    width: 238px;
    justify-content: center;
    align-items: center;
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .advantages__item {
    width: 238px;
    justify-content: center;
    align-items: center;
    margin: 0;
    margin-top: -120px;

    &::before {
      content: "";
      position: relative;
      width: 100%;
      height: 382px;
      right: 23px;
      bottom: -25px;
      background-image: url("../img/iphone-screen-2-desktop@1x.png");
      background-repeat: no-repeat;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../img/iphone-screen-2-desktop@2x.png");
      }
    }
  }

  .advantages__item:last-child {
    margin-right: 162px;

    &::before {
      content: "";
      position: relative;
      width: 100%;
      height: 382px;
      right: 23px;
      bottom: -25px;
      background-image: url("../img/iphone-screen-3-desktop@1x.png");
      background-repeat: no-repeat;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../img/iphone-screen-3-desktop@2x.png");
      }
    }
  }

  .advantages__item:first-child {
    margin-left: 160px;

    &::before {
      content: "";
      position: relative;
      width: 100%;
      height: 382px;
      right: 26px;
      bottom: -25px;
      background-image: url("../img/iphone-screen-1-desktop@1x.png");
      background-repeat: no-repeat;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../img/iphone-screen-1-desktop@2x.png");
      }
    }
  }
}

.advantages__title {
  display: flex;
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
  color: @bg-black;
  text-align: center;
  margin: 0;
  margin-bottom: 11px;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .advantages__title {
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .advantages__title {
    text-align: center;
    font-size: 30px;
    line-height: 30px;
  }
}

.advantages__description {
  font-size: 14px;
  line-height: 24px;
  color: @bg-dark;
  text-align: center;
  margin: 0;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .advantages__description {
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .advantages__description {
    text-align: center;
    font-size: 18px;
    line-height: 30px;
    margin-top: 14px;
  }
}
