.main-nav__close {
  position: absolute;
  width: 24px;
  height: 23px;
  cursor: pointer;
  border: none;
  outline: none;
  background: transparent;
  margin: 0;
  top: 22px;
  right: 41px;
  padding: 0;
}
.main-nav__close:hover::before {
  background-color: #d22856;
}
.main-nav__close:hover::after {
  background-color: #d22856;
}
.main-nav__close:active::before {
  background-color: rgba(210, 40, 86, 0.3);
}
.main-nav__close:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.main-nav__close::before {
  content: "";
  position: absolute;
  top: 9px;
  left: -3px;
  width: 30px;
  height: 4px;
  border-radius: 1px;
  background-color: #ffffff;
  transform: rotate(45deg);
}
.main-nav__close::after {
  content: "";
  position: absolute;
  top: 9px;
  left: -3px;
  width: 30px;
  height: 4px;
  border-radius: 1px;
  background-color: #ffffff;
  transform: rotate(-45deg);
}
@media (min-width: 660px) {
