.checkbox-input + label::before {
  content: "";
  display: flex;
  position: absolute;
  width: 35px;
  height: 35px;
  top: -7px;
  left: -70px;
  margin: 0;
  padding: 0;
  border: solid 2px #e5e5e5;
  border-radius: 3px;
  background-color: #ffffff;
}
.checkbox-input + label:hover::before {
  border: solid 2px #283645;
}
.checkbox-input + label:active::before {
  border: solid 2px #d22856;
}
@media (min-width: 660px) {
  .checkbox-input + label::before {
    left: -60px;
  }
}
.checkbox-input:checked + label::before {
  background-image: url("../img/icon-yes.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.sistem-input + label::before {
  content: "";
  position: absolute;
  width: 37px;
  height: 36px;
  top: -7px;
  left: -70px;
  margin: 0;
  padding: 0;
  border: solid 2px #e5e5e5;
  border-radius: 50%;
  background-color: #ffffff;
}
.sistem-input + label:hover::before {
  border: solid 2px #283645;
}
.sistem-input + label:active::before {
  border: solid 2px #d22856;
}
@media (min-width: 660px) {
  .sistem-input + label::before {
    left: -60px;
  }
}
.sistem-input:checked + label::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  top: 5px;
  left: -57px;
  background: #d22856;
  border-radius: 50%;
}
@media (min-width: 660px) {
  .sistem-input:checked + label::after {
    left: -47px;
  }
}
